import {
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  TableContainer,
  Stack,
  Heading,
  Box,
  Input,
  Textarea,
  Button,
} from "@chakra-ui/react";
import React from "react";

const AddDocument = () => {
  return (
    <Stack h="100%" mt={1} align="center" justify="center">
      <Box px={8} pt={4} width={"30rem"}>
        <Box mb={4}>
          <Input
            placeholder="Enter Title"
            size="base"
            p={1}
            rounded={"md"}
            maxWidth={"30rem"}
            py={"0.6rem"}
          />
        </Box>
        <Box mb={4}>
          <Input
            placeholder="Enter Title"
            size="base"
            p={1}
            rounded={"md"}
            maxWidth={"30rem"}
            py={"0.6rem"}
            type="file"
          />
        </Box>
        <Box>
          <Textarea placeholder="Enter Content" maxWidth={"30rem"} />
        </Box>
        <Box
          my={"6"}
          display={"flex"}
          justifyContent={"flex-end"}
          alignItems={"center"}
        >
          <Button
            loadingText="Please wait..."
            type="submit"
            size={"sm"}
            _hover={{ bg: "#FF9000" }}
            w="8.188rem"
            bg="#FF9000"
            borderRadius={"0.25rem"}
            color="whiteAlpha.900"
            fontWeight={"700"}
            fontSize={"1rem"}
          >
            Submit
          </Button>
        </Box>
      </Box>
      <Heading as="h4" size="md" textAlign={"center"} my={4}>
        Whatsapp Rules
      </Heading>
      <TableContainer
        style={{
          margin: "auto",
          border: "1px solid gray",
          textAlign: "center",
        }}
      >
        <Table size="sm">
          <Thead>
            <Tr>
              <Th style={{ borderBottom: "1px dotted #000000" }}>S.no</Th>
              <Th style={{ borderBottom: "1px dotted #000000" }}>Meaning</Th>
              <Th style={{ borderBottom: "1px dotted #000000" }}>Code</Th>
            </Tr>
          </Thead>
          <Tbody>
            {data?.map((el, i) => {
              return (
                <Tr key={i}>
                  <Td style={{ borderBottom: "1px dotted #000000" }}>
                    {el.sn}
                  </Td>
                  <Td style={{ borderBottom: "1px dotted #000000" }}>
                    {el.code}
                  </Td>
                  <Td style={{ borderBottom: "1px dotted #000000" }}>
                    {el.msg}
                  </Td>
                </Tr>
              );
            })}
          </Tbody>
        </Table>
      </TableContainer>
    </Stack>
  );
};

export default AddDocument;

const data = [
  { sn: "1", code: "{{user}}", msg: "Name from the Lead" },
  {
    sn: "2",
    code: "{{userName}}",
    msg: "Name of the user who is sending the message",
  },
  {
    sn: "3",
    code: "{{userPhone}}",
    msg: "Phone Number of the user who is sending the message",
  },
  {
    sn: "4",
    code: "*text*",
    msg: "To bold the text wrap the text with a asterisk(*)",
  },
  {
    sn: "5",
    code: "_text_",
    msg: "To italicize your message, place an underscore on both sides ",
  },
  {
    sn: "6",
    code: "text",
    msg: "To monospace your message, place three backticks on both ",
  },
];
